.gpt3__header{
    display: flex;   
}

.gpt3__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt3__header-content h1{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    letter-spacing: 0.48px;
}

.gpt3__header-content p{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: normal;
    line-height: 27px;
}

.gpt3__header-content__input{
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt3__header-content__input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    border-radius: 5px 0px 0px 5px;
    background: var(--color-footer);
    color: #fff;
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: 400;
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.gpt3__header-content__input button{
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    line-height: 27px;
    background: #FF4820;
    color: #FFF;
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: bold;
    padding: 0 1rem;
    outline: none;
    cursor: pointer;
    border: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.gpt3__header-content__people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}

.gpt3__header-content__people img{
    width: 181.786px;
    height: 38px;
    flex-shrink: 0;
}

.gpt3__header-content__people p{
    color: #FFF;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 37.611px;
    text-align: center;
    margin: 0 0 0 1rem;
}

.gpt3__header-img{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-img img{
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .gpt3__header {
        flex-direction: column;
    }

    .gpt3__header-content{
        margin: 0 0 3rem;
    }
}

@media screen and (min-width: 650px) {
    .gpt3__header h1{
        font-size: 30px;
        line-height: 40px;
    }

    .gpt3__header p{
        font-size: 16px;
        line-height: 24px;
    }

    .gpt3__header-content__people{
        flex-direction: column;
    }

    .gpt3__header-content__people p{
        margin: 0;
    }

    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (min-width: 490px) {
    .gpt3__header h1{
        font-size: 36px;
        line-height: 48px;
    }

    .gpt3__header p{
        font-size: 14px;
        line-height: 24px;
    }

    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 12px;
        line-height: 16px;
    }
}